 
import {useState} from 'react';
import Header from './component/Header'
import Tasks from './component/Tasks';
import AddTask from './component/AddTask';
import Modal from './component/Modal'
const header = "Task Tracker";



function App() {
  const [showModal,setshowModal] = useState(false);
 


const [showAddTask,setShowAddTask]= useState(false)

  const [tasks, setTasks] = useState([
    {
        id:1,
        text:'Doctors Appointemnt',
        day:'Feb 5th at 2:30pm',
        reminder:false,
    },
    {
       id:2,
       text:'school meeting',
       day:'Feb 6th at 1:30pm',
       reminder:true,
   },
   {
       id:3,
       text:'shopping',
       day:'Feb 5th at 2:30pm',
       reminder:false,
   }
])


  //delete task
const deleteTask = (id)=>
{
  setTasks(tasks.filter((item)=> item.id !== id))
}
//delte all 
const deleteAll = ()=>
{
 setTasks("")
}

//remainder
const toggleReminder = (id) =>{
  setTasks(tasks.map((task)=>
    task.id === id ? { ...task, reminder:!task.reminder}:task
  ))
}
//add task
const addTask = (task)=>{
  const id = Math.floor(Math.random()*10000)+1
  const newTask ={id,...task}
  setTasks([...tasks,newTask])
   
}

//open modal
const openModal =()=>{
     
  setshowModal(showModal =>!showModal)

  
}
  return (
   <>
    <Modal showModal={showModal}  setshowModal={setshowModal} onAdd={addTask} />
   
    <div className="container">

      <Header  
     openModal={openModal}
       onclick = {()=> setShowAddTask(!showAddTask) }
      title ={ header}
       />

     {showAddTask && <AddTask   onAdd={addTask}/>} 

      {
        //if
        tasks.length >  0?
        //if tasks.length > 0 it will be return this:// "after ? the true return,after : is the false return 
        <Tasks
        tasks={tasks} 
        onDelete={deleteTask}
        deleteAll={deleteAll}
        handelReminder={toggleReminder} 
         />
        //else it will return this message
        : <h4 style={{color:'#fff'}}>No Tasks To Show ! </h4>

      }

    </div>
    
    </>

  );
}

export default App;
