//sniappets to creat arrow function for react =>race

// import Button from './Button'
import { FaTasks } from 'react-icons/fa';
import styled from 'styled-components';
import {IoIosAddCircleOutline} from 'react-icons/io';
 

const ButtonModal = styled.button`
display: inline-block;
background: #2DA94F;
color: #fff;
border: none;
padding: 5px 10px;
margin: 5px;
border-radius: 5px;
cursor: pointer;
text-decoration: none;
font-size: 20px;
font-family: inherit;
&:focus {
    outline: none;
  }
`




const Header = ({title , onclick ,openModal}) => {
 
    return (
       <header className='header'>
           <h1 className="heading" ><FaTasks/>  {title}</h1>
           {/* <Button onclick={onclick}  text ='Add' /> */}
           <ButtonModal onClick={openModal}>
           <IoIosAddCircleOutline style={{fontSize : "20px", marginBottom:"-4px"}}/>
               Add Task</ButtonModal>
        
        
       </header>
    )
}
 
Header.defaultProps ={
    title:'Task Tracker'
}
// const headingStyle = {
//     color: "red",
//     backgroundColor:"#333",
//     padding: "10px"
// }

export default Header
